@use 'base/variables' as v;
@use 'base/mixins' as m;

.caracteristicas {

    &__heading {
        text-align: center;
        @include m.barraHeading;

        &::before {
            background-color: v.$darkGrayishViolet;
            margin: 0 auto 4rem auto;

            @include m.desktop { 
                margin: 0 0 4rem;
            }
        }

        @include m.desktop { 
            text-align: left;
        }
    }

    &__contenido {
        margin-top: 5rem;
        @include m.grid(1, 5rem);

        @include m.tablet { 
            @include m.grid(2, 5rem);
        }

        @include m.desktop { 
            @include m.grid(3, 5rem);
        }
    }
}

.card {

    &:nth-child(3) {
        @include m.tablet { 
            grid-column: 1 / 3;
        }

        @include m.desktop { 
            grid-column: unset;
        }
    }

    &__imagen {
        width: 7rem;
        margin: 0 auto 2rem auto;

        @include m.desktop { 
            margin: 0 0 2rem;
        }
    }

    &__contenido {
        text-align: center;

        @include m.desktop { 
            text-align: left;
        }
    }
}