@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fontBody;
    font-size: 1.6rem;
    color: v.$darkGrayishViolet;

    &[data-noScroll="true"] {
        overflow: hidden;
    }
}
p {
    line-height: 1.7;
    margin: 0;
}
.contenedor {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: 0 0 2rem  0;
    font-weight: normal;
    font-family: v.$fontHeading;
    color: v.$verydarkViolet;

}
h1 {
    font-size: 4.5rem;

    @include m.desktop { 
        font-size: 6.2rem;
    }
}

h2 {
    font-size: 3.8rem;
}

h3 {
    font-size: 3rem;
}
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}