@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: 3rem 0;

    &__barra {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__btn-toggle {
        display: block;
        position: absolute;
        right: 2rem;
        width: 3rem;
        height: 3rem;
        border: none;
        background-color: transparent;
        background-image: url(../img/icon-hamburger.svg);
        background-repeat: no-repeat;
        background-size: 3rem;

        @include m.tablet { 
            display:none;
        }

        &[aria-expanded="true"] {
            background-image: url(../img/icon-close.svg);
        }
    }


}

.navegacion {
    
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    gap: 5rem;
    inset: 12% 0 0 0; // Funciona con top: 0, right: 0; left: 0, bottom: 0
    background-color: v.$verydarkViolet;
    padding: 8rem 2rem;
    z-index: 1000;
    background-image: url(../img/bg-pattern-mobile-nav.svg);
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: contain;
    transform: translateX(-100%);

    transition-property: transform;
    transition-duration: .3s;

    &[data-visible="true"] {
        transform: translateX(0%);
    }

    
    @include m.tablet { 
        flex-direction: row;
        position: unset;
        padding: 0;
        background-color: transparent;
        background-image: none;
        transform: translateX(0%);
    }

    li {

        &:last-of-type {
            display: flex;
            align-self: stretch;

             a {
                background-color: v.$verydarkViolet;
                color: v.$white;
                border: 1px solid v.$white;
                padding: 1rem 2.5rem;
                width: 100%;
                display: block;
                text-align: center;

                @include m.tablet { 
                    @include m.boton(v.$white, v.$black);
                }
             }
        }

        a {
            color: v.$white;
            text-transform: uppercase;
            font-weight: bold;  

            @include m.tablet { 
                color: v.$darkGrayishViolet; 

                &:hover {
                    color: v.$black;
                }
            }
        }
    }
}