.text-center {
  text-align: center;
}

.section-pd {
  padding: 3rem 0;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Karla", sans-serif;
  font-size: 1.6rem;
  color: #837d87;
}
body[data-noScroll=true] {
  overflow: hidden;
}

p {
  line-height: 1.7;
  margin: 0;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
  font-weight: normal;
  font-family: "DM Serif Display", serif;
  color: #2b272f;
}

h1 {
  font-size: 4.5rem;
}
@media (min-width: 1100px) {
  h1 {
    font-size: 6.2rem;
  }
}

h2 {
  font-size: 3.8rem;
}

h3 {
  font-size: 3rem;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  padding: 3rem 0;
}
.header__barra {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__btn-toggle {
  display: block;
  position: absolute;
  right: 2rem;
  width: 3rem;
  height: 3rem;
  border: none;
  background-color: transparent;
  background-image: url(../img/icon-hamburger.svg);
  background-repeat: no-repeat;
  background-size: 3rem;
}
@media (min-width: 768px) {
  .header__btn-toggle {
    display: none;
  }
}
.header__btn-toggle[aria-expanded=true] {
  background-image: url(../img/icon-close.svg);
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  gap: 5rem;
  inset: 12% 0 0 0;
  background-color: #2b272f;
  padding: 8rem 2rem;
  z-index: 1000;
  background-image: url(../img/bg-pattern-mobile-nav.svg);
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: contain;
  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.3s;
}
.navegacion[data-visible=true] {
  transform: translateX(0%);
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    position: unset;
    padding: 0;
    background-color: transparent;
    background-image: none;
    transform: translateX(0%);
  }
}
.navegacion li:last-of-type {
  display: flex;
  align-self: stretch;
}
.navegacion li:last-of-type a {
  background-color: #2b272f;
  color: #fff;
  border: 1px solid #fff;
  padding: 1rem 2.5rem;
  width: 100%;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion li:last-of-type a {
    background-color: #fff;
    font-size: 1.6rem;
    text-transform: uppercase;
    padding: 1rem 2.5rem;
    font-weight: 700;
    letter-spacing: 1px;
    text-align: center;
    color: #000;
    text-decoration: none;
    transition: background-color 0.3s ease;
    border: none;
    border: 1px solid #000;
  }
  .navegacion li:last-of-type a:hover {
    cursor: pointer;
    color: #fff;
    background-color: #000;
  }
}
.navegacion li a {
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
}
@media (min-width: 768px) {
  .navegacion li a {
    color: #837d87;
  }
  .navegacion li a:hover {
    color: #000;
  }
}

.hero {
  margin-bottom: 10rem;
}
@media (min-width: 1100px) {
  .hero {
    margin-bottom: 25rem;
  }
}
.hero__imagen-m {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .hero__imagen-m {
    display: none;
  }
}
.hero__bg {
  background-color: #2d2640;
  position: relative;
}
.hero__bg::after {
  content: "";
  background-image: url(../img/bg-pattern-intro-left-mobile.svg);
  background-repeat: no-repeat;
  background-position: top left;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
@media (min-width: 768px) {
  .hero__bg::after {
    background-image: url(../img/bg-pattern-intro-left-desktop.svg);
    top: 70%;
  }
}
.hero__bg::before {
  content: "";
  background-image: url(../img/bg-pattern-intro-right-mobile.svg);
  background-repeat: no-repeat;
  background-position: bottom right;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: -30%;
}
@media (min-width: 768px) {
  .hero__bg::before {
    background-image: url(../img/bg-pattern-intro-right-desktop.svg);
    background-position: top right;
    bottom: 0;
  }
}
.hero__contenido {
  padding: 10rem 0;
  z-index: 1;
  position: relative;
}
@media (min-width: 768px) {
  .hero__contenido {
    width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1100px) {
  .hero__contenido {
    width: 100%;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    padding: 15rem 0;
  }
}
.hero__info {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__info {
    margin-bottom: 4rem;
  }
}
@media (min-width: 1100px) {
  .hero__info {
    text-align: left;
    margin: 0;
  }
}
.hero__heading {
  color: #fff;
}
@media (min-width: 1100px) {
  .hero__heading::before {
    content: "";
    background-color: #fff;
    width: 15rem;
    height: 0.1rem;
    display: block;
    margin-bottom: 4rem;
  }
}
.hero__enlace {
  background-color: #000;
  font-size: 1.6rem;
  text-transform: uppercase;
  padding: 1rem 2.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 1px solid #000;
  display: inline-block;
  margin-top: 2rem;
  background-color: transparent;
  border: 1px solid #fff;
}
.hero__enlace:hover {
  cursor: pointer;
  color: #fff;
  background-color: #000;
}
.hero__enlace:hover {
  background-color: #fff;
  color: #000;
}
.hero__imagen-c {
  overflow: hidden;
}
@media (min-width: 768px) {
  .hero__imagen-c {
    width: 60%;
    margin: 0 auto;
  }
}
@media (min-width: 1100px) {
  .hero__imagen-c {
    width: 100%;
    margin-bottom: -30rem;
  }
}
.hero__imagen-d {
  display: none;
}
@media (min-width: 768px) {
  .hero__imagen-d {
    display: block;
    width: 100%;
    height: auto;
    transition: transform 0.3s;
  }
}
.hero__imagen-d:hover {
  transform: scale(1.1);
}

.caracteristicas__heading {
  text-align: center;
}
.caracteristicas__heading::before {
  content: "";
  background-color: #fff;
  width: 15rem;
  height: 0.1rem;
  display: block;
  margin-bottom: 4rem;
}
.caracteristicas__heading::before {
  background-color: #837d87;
  margin: 0 auto 4rem auto;
}
@media (min-width: 1100px) {
  .caracteristicas__heading::before {
    margin: 0 0 4rem;
  }
}
@media (min-width: 1100px) {
  .caracteristicas__heading {
    text-align: left;
  }
}
.caracteristicas__contenido {
  margin-top: 5rem;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .caracteristicas__contenido {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1100px) {
  .caracteristicas__contenido {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 768px) {
  .card:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (min-width: 1100px) {
  .card:nth-child(3) {
    grid-column: unset;
  }
}
.card__imagen {
  width: 7rem;
  margin: 0 auto 2rem auto;
}
@media (min-width: 1100px) {
  .card__imagen {
    margin: 0 0 2rem;
  }
}
.card__contenido {
  text-align: center;
}
@media (min-width: 1100px) {
  .card__contenido {
    text-align: left;
  }
}

.nosotros {
  margin-top: 13rem;
}
.nosotros__contenido {
  background-image: url(../img/bg-pattern-how-we-work-mobile.svg);
  background-color: #2d2640;
  background-repeat: no-repeat;
  background-position: top right;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1100px) {
  .nosotros__contenido {
    background-image: url(../img/bg-pattern-how-we-work-desktop.svg);
    flex-direction: row;
    justify-content: space-between;
  }
}
.nosotros__heading {
  text-align: center;
  color: #fff;
}
@media (min-width: 1100px) {
  .nosotros__heading {
    width: 40rem;
    text-align: left;
    margin: 0;
  }
}
.nosotros__enlace {
  background-color: #000;
  font-size: 1.6rem;
  text-transform: uppercase;
  padding: 1rem 2.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 1px solid #000;
  background-color: transparent;
  border: 1px solid #fff;
}
.nosotros__enlace:hover {
  cursor: pointer;
  color: #fff;
  background-color: #000;
}
.nosotros__enlace:hover {
  background-color: #fff;
  color: #000;
}

.footer {
  margin-top: 13rem;
  background-color: #fafafa;
  background-image: url(../img/bg-pattern-footer-mobile.svg);
  background-repeat: no-repeat;
}
@media (min-width: 1100px) {
  .footer {
    background-image: url(../img/bg-pattern-footer-desktop.svg);
  }
}
.footer__contenido {
  padding: 5rem 0;
}
.footer__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  margin-bottom: 4rem;
}
@media (min-width: 1100px) {
  .footer__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__sociales {
  display: flex;
  gap: 1rem;
}
.footer__iconos {
  font-size: 2.2rem;
  color: #837d87;
}
.footer__iconos:hover {
  color: #000;
}

.enlaces {
  border-top: 1px solid #837d87;
  padding-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .enlaces {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: flex-start;
  }
}
@media (min-width: 1100px) {
  .enlaces {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.enlaces__ul {
  text-align: center;
}
@media (min-width: 1100px) {
  .enlaces__ul {
    text-align: left;
  }
}
.enlaces__label {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 2rem;
  letter-spacing: 1px;
}
.enlaces__li {
  margin-bottom: 1rem;
}
.enlaces__li:last-of-type {
  margin: 0;
}
.enlaces__a {
  text-transform: uppercase;
  color: #000;
  font-weight: bold;
  letter-spacing: 1px;
  position: relative;
}
.enlaces__a::after {
  content: "";
  background-color: #000;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(0);
  transform-origin: left;
  transition: transform 0.5s;
}
.enlaces__a:hover::after {
  transform: scale(1);
  transform-origin: right;
}

.autor {
  text-align: center;
}
.autor span {
  display: block;
  color: #2d2640;
  font-style: italic;
}