@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero {
    margin-bottom: 10rem;

    @include m.desktop { 
        margin-bottom: 25rem;
    }

    &__imagen-m {
        width: 100%;
        height: auto;
        
        @include m.tablet { 
            display: none;
        }
    }

    &__bg {
        background-color: v.$darkViolet;
        position: relative;

        &::after {
            content: '';
            background-image: url(../img/bg-pattern-intro-left-mobile.svg);
            background-repeat: no-repeat;
            background-position: top left;
            width: 100%;
            height: 100%;

            position: absolute;
            top: 0;

            @include m.tablet { 
                background-image: url(../img/bg-pattern-intro-left-desktop.svg);
                top: 70%;
            }
        }

        &::before {
            content: '';
            background-image: url(../img/bg-pattern-intro-right-mobile.svg);
            background-repeat: no-repeat;
            background-position: bottom right;
            width: 100%;
            height: 100%;

            position: absolute;
            bottom: -30%;

            @include m.tablet { 
                background-image: url(../img/bg-pattern-intro-right-desktop.svg);
                background-position: top right;
                bottom: 0;
            }
        }
    }


    &__contenido {
        padding: 10rem 0;
        z-index: 1;
        position: relative;

        @include m.tablet { 
            width: 80%;
            margin: 0 auto;
        }

        @include m.desktop { 
            width: 100%;
            @include m.grid(2, 5rem);
            padding: 15rem 0;
        }
    }

    &__info {
        color: v.$white;
        text-align: center;

        @include m.tablet { 
            margin-bottom: 4rem;
        }

        @include m.desktop { 
            text-align: left;
            margin: 0;
        }
    }

    &__heading {
        color: v.$white;

        @include m.desktop { 
            @include m.barraHeading;
        }
    }

    &__enlace {
        @include m.boton(v.$black, v.$white);
        display: inline-block;
        margin-top: 2rem;
        background-color: transparent;
        border: 1px solid v.$white;

        &:hover {
            background-color: v.$white;
            color: v.$black;
        }
    }

    &__imagen-c {
        overflow: hidden;

        @include m.tablet { 
            width: 60%;
            margin: 0 auto;
        }

        @include m.desktop { 
            width: 100%;
            margin-bottom: -30rem;
        }
    }

    &__imagen-d {
        display: none;

        @include m.tablet { 
            display: block;
            width: 100%;
            height: auto;
            transition: transform .3s;
        }

        &:hover {
            transform: scale(1.1);
        }
    }
}