@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 13rem;
    background-color: v.$verylightGray;
    background-image: url(../img/bg-pattern-footer-mobile.svg);
    background-repeat: no-repeat;

    @include m.desktop { 
        background-image: url(../img/bg-pattern-footer-desktop.svg);
    }

    &__contenido {
        padding: 5rem 0;
    }

    &__barra {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
        margin-bottom: 4rem;

        @include m.desktop { 
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__sociales {
        display: flex;
        gap: 1rem;
    }

    &__iconos {
        font-size: 2.2rem;
        color: v.$darkGrayishViolet;

        &:hover {
            color: v.$black;
        }
    }
}

.enlaces {
    border-top: 1px solid v.$darkGrayishViolet;
    padding-top: 4rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;

    @include m.tablet { 
        @include m.grid(2, 5rem);
        align-items: flex-start;
    }

    @include m.desktop { 
        @include m.grid(4, 2rem);
    }

    &__ul {
        text-align: center;

        @include m.desktop { 
            text-align: left;
        }
    }

    &__label {
        font-weight: bold;
        text-transform: uppercase;
        margin-bottom: 2rem;
        letter-spacing: 1px;
    }

    &__li {
        margin-bottom: 1rem;

        &:last-of-type {
            margin: 0;
        }
    }

    &__a {
        text-transform: uppercase;
        color: v.$black;
        font-weight: bold;
        letter-spacing: 1px;

        position: relative;

        &::after {
            content: '';
            background-color: v.$black;
            width: 100%;
            height: 2px;
            position: absolute;
            bottom: 0;
            left: 0;
            transform: scale(0);
            transform-origin: left;

            transition: transform .5s;
        }

        &:hover::after {
            transform: scale(1);
            transform-origin: right;
        }
    }
}

.autor {
    text-align: center;

    span {
        display: block;
        color: v.$darkViolet;
        font-style: italic;
    }
}